	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Job Security","date":"11 Nov 2003","slug":"/2003-11-11-job-security/","tags":"Y2003"},"html":"<p>At work we have typical German work contracts which can be terminated with 6 weeks notice before the end of a quarter. The next termination date is next Wednesday, and there are rumors that there will be layoffs announced for the end of the year, despite our having worked short time for the past 6 months. One of sysadmins decided not to wait and found something new. That makes the jobs for the rest of us sysadmins more secure (assuming we stay in business), but it also means we all have more work. And more bullet points for the CV.</p>\n<p>Among other things, I'll now be responsible for all our Linux installations, about 60 workstations and a dozen servers. I'll be the resident Linux expert, except that I don't feel like an expert at all. Sure, I've been using Linux for nearly 10 years, but I haven't been keeping up with the latest developments, and lately I've been happy to run a stock commerical distribution (Mandrake at work, SuSE at home). So I have some learning to do. At least it beats learning Windows.</p>"},"next":{"fields":{"title":"Hohmann Go Home","date":"11 Nov 2003","slug":"/2003-11-11-hohmann-go-home/"}},"previous":{"fields":{"title":"Progress","date":"11 Nov 2003","slug":"/2003-11-11-progress/"}}},{"node":{"fields":{"title":"Hohmann Go Home","date":"11 Nov 2003","slug":"/2003-11-11-hohmann-go-home/","tags":"Y2003"},"html":"<p>When I first <a title=\"PapaScott: Not-so-latent Antisemitism\" href=\"http://www.papascott.de/2003/10/31/2674.php\">posted</a> about the anti-semitic remarks by CDU Bundestag member Martin Hohmann, I thought it would be a back page story that would be forgotten the next day. I even tried to push the story to other blogs to make sure it would not go unnoticed. How wrong I was. Pressure has built on the CDU leadership to go beyond censuring Homann, especially after Defence Minister Struck <a title=\"BBC NEWS | Europe | German forces 'not anti-Semitic'\" href=\"http://news.bbc.co.uk/2/hi/europe/3245039.stm\">fired</a> the general in charge of Germany's special forces (KSK) for supporting Hohmann. </p>\n<p>The CDU leadership has now buckled, and has  <a title=\"BBC NEWS | Europe | Party expels MP over Jewish row\" href=\"http://news.bbc.co.uk/2/hi/europe/3259179.stm\">started</a> the process of expelling Hohmann from the Bundestag faction and the party. Late, Frau Merkel, but not too late. You can take your ear off the ground now.</p>"},"next":{"fields":{"title":"Sophie Scholl","date":"10 Nov 2003","slug":"/2003-11-10-sophie-scholl/"}},"previous":{"fields":{"title":"Job Security","date":"11 Nov 2003","slug":"/2003-11-11-job-security/"}}},{"node":{"fields":{"title":"Sophie Scholl","date":"10 Nov 2003","slug":"/2003-11-10-sophie-scholl/","tags":"Y2003"},"html":"<p><a title=\"and some people she insprired\" href=\"http://www.billspricht.net/2003_11_09_archive.html#106840443547943729\">Sophie Scholl</a></p>"},"next":{"fields":{"title":"Nazi gas firm IG Farben to file for insolvency","date":"10 Nov 2003","slug":"/2003-11-10-nazi-gas-firm-ig-farben-to-file-for-insolvency/"}},"previous":{"fields":{"title":"Hohmann Go Home","date":"11 Nov 2003","slug":"/2003-11-11-hohmann-go-home/"}}}],"pathPrefix":"","first":false,"last":false,"index":590,"pageCount":1101}}

